'use strict';

angular.module('DEVBYCM.cmSideMenuBox', [])
  .directive('cmSideMenuBox', function () {
    return {
      restrict: 'E',
      template: '<div ng-transclude></div>',
      transclude: true,
      link: function () {
      }
    };
  })
  .directive('cmSideMenu', function () {
    return {
      restrict: 'E',
      template: '<ng-include src="\'app/components/ui/cmNavBar/template.html\'"></ng-include>\n<div ng-transclude style="position: absolute;top: 45px;right: 0;bottom: 0;left: 0;overflow-y: auto"></div>',
      transclude: true
    };
  })
  .directive('cmSideMenuContent', function () {
    return {
      restrict: 'E'
    };
  })
  .directive('cmSideMenuContentHeader', function () {
    return {
      restrict: 'EA',
      scope:false,
      template: '<div ng-transclude></div>',
      transclude: true,
      link: function (scope, element) {
        /*
         //通过调整父元素margin来实现，在被wrap的页面会失效
         element.parent().css('padding-top',parseInt($window.getComputedStyle(element[0].parentElement, null).paddingTop.slice(0,-2)) + element.prop('offsetHeight')+'px');
         */
        //添加一个替代元素来虚拟一个初始高度
        element.after('<div></div>');
        element.next().css('height',element.prop('offsetHeight')+'px');
        element.css('top', element.parent()[0].scrollTop+'px');
        element.parent().on('scroll', function () {
          element.css('top', element.parent()[0].scrollTop+'px');
        });
      }
    };
  })
;

