'use strict';
angular.module('DEVBYCM.cmNotificationAreaModule', [])
  .directive('cmPerson',function($rootScope,AccountSvc,LoginSvc,cmLoginSvc,cmLoadingSvc){
    return {
      restrict: 'E',
      templateUrl: 'app/components/ui/cmNotificationArea/template.html',
      scope: {},
      link: function (scope) {
        scope.uiIsPanelShow = false;
        scope.currentUser = AccountSvc.getCurrentUser();
        scope.funcSwitchUser = function(){
          scope.uiIsPanelShow = false;
          cmLoginSvc.show();
        };
        scope.funcLogout = function(){
          scope.uiIsPanelShow = false;
          cmLoadingSvc.setContent('正在登出...','inProgress');
          cmLoadingSvc.show();
          LoginSvc.logout().then(function(){
            cmLoadingSvc.setContent('您已注销','success');
          },function(){
            cmLoadingSvc.setContent('登出失败','error');
          }).finally(function(){
            cmLoadingSvc.hide();
          })
        };
        $rootScope.$on('cmLogin',function(){
          scope.currentUser = AccountSvc.getCurrentUser();
        })
      }
    };
  })
;

