'use strict';

angular.module('DEVBYCM.cmNavMenu', [])
  .directive('cmNavMenu', function (cmNavBarService,$rootScope,$state) {
    return {
      restrict: 'E',
      templateUrl: 'app/components/ui/cmNavMenu/template.html',
      transclude: true,
      controller: function(){
      },
      link: function(scope) {
        scope.menuItems=cmNavBarService.getMenu();
        scope.show=false;
        scope.goAndClose = function(state,params){
          $state.go(state,params);
          scope.show=false;
        };
        scope.close = function(){
          scope.show=false;
        };
        $rootScope.$on('cmNavMenu',function(e,v){
          v && !angular.isUndefined(v['toggle']) && (scope.show=!scope.show);
        });
      }
    };
  })
;

