'use strict';

angular.module('DEVBYCM.cmLoginModule', [])
  .directive('cmLogin', function ($rootScope, cmLoginSvc, LoginSvc) {
    return {
      restrict: 'E',
      templateUrl: 'app/components/ui/cmLogin/template.html',
      scope: {},
      link: function (scope) {
        scope.isShow = false;
        scope.isLoggedIn = LoginSvc.isLoggedIn();
        scope.funcTryToClose = function () {
          if (scope.isLoggedIn) {
            scope.isShow = false;
          }
        };
        $rootScope.$on('cmLogin', function (e, v) {
          v && !angular.isUndefined(v['show']) && (scope.isShow = v['show']);
          scope.isLoggedIn = LoginSvc.isLoggedIn();
        });
      }
    };
  })
  .factory('cmLoginSvc', function ($rootScope, $timeout) {
    return {
      'show': function () {
        $rootScope.$broadcast('cmLogin', {'show': true});
      },
      'hide': function (delay) {
        angular.isUndefined(delay) && (delay = 1000);
        return $timeout(function () {
          $rootScope.$broadcast('cmLogin', {'show': false});
        }, delay);
      }
    };
  })
  .controller('cmLoginCtrl', function ($scope, $state, cmLoginSvc, LoginSvc) {
    $scope.uiIsHover = false;
    $scope.uiCurrentHoverIndex = null;
    $scope.uiIsLoggingIn = false;
    $scope.uiIsInputError = false;
    var defaultAvatar = 'data:image/png;base64,';
    var emptyUser = {
      'Id': null,
      'Password': null,
      'Avatar':defaultAvatar,
      'AutoUpdateSavedUser': false
    };
    $scope.selectedUser = angular.copy(emptyUser);
    $scope.savedUsers = LoginSvc.getSavedUsers();
    $scope.uiWhichPanel = 'switch';
    var funcDisplayUserLogin = function (user) {
      $scope.uiWhichPanel = 'login';
      $scope.selectedUser = angular.copy(user);
    };
    //存储用户不足两个（可供挑选）时，自动加载一个
    (function humanizedViewSwitch(){
      if($scope.savedUsers.length===0){
        funcDisplayUserLogin(emptyUser);
      }else if($scope.savedUsers.length===1){
        funcDisplayUserLogin($scope.savedUsers[0]);
      }
    })();
    //$scope.savedUsers = [$scope.savedUsers[0]];
    //View functions
    $scope.funcRemoveUser = function (index) {
      $scope.savedUsers.splice(index, 1);
      LoginSvc.setSavedUsers($scope.savedUsers);
    };
    $scope.funcDisplayUserSwitch = function () {
      $scope.uiWhichPanel = 'switch';
    };
    $scope.funcDisplayNewUserLogin = function () {
      $scope.uiWhichPanel = 'login';
      funcDisplayUserLogin(emptyUser);
    };
    $scope.funcDisplayUserLogin = function (user) {
      funcDisplayUserLogin(user);
    };
    $scope.funcUserIdChanged = function () {
      $scope.selectedUser.Avatar = defaultAvatar;
    };
    $scope.funcCancelInputErrorMark = function () {
      $scope.uiIsInputError = false;
    };
    $scope.funcLogin = function () {
      $scope.uiIsLoggingIn = true;
      var user = $scope.selectedUser;
      var funcAddOrUpdateUser = function (user) {
        var savedUsers = $scope.savedUsers;
        //-1 默认表示还没有存储这个用户
        var userExistIndex = -1;
        angular.forEach(savedUsers, function (savedUser, index) {
          if (savedUser.Id === user.Id) {
            userExistIndex = index;
          }
        });
        if (userExistIndex ===-1) {
          savedUsers.push(user);
        } else {
          savedUsers[userExistIndex] = user;
        }
        $scope.savedUsers = savedUsers;
        LoginSvc.setSavedUsers(savedUsers);
      };
      LoginSvc.login(user.ID, user.Password)
        .then(function (User) {
          if (user.AutoUpdateSavedUser) {
            User.AutoUpdateSavedUser = true;
            User.Password = user.Password;
            funcAddOrUpdateUser(User);
          }
          $scope.uiWhichPanel = 'success';
          cmLoginSvc.hide(1500).then(function(){
            $scope.funcDisplayUserSwitch();
            $state.reload();
          });
        }, function () {
          $scope.uiIsInputError = true;
        }).finally(function () {
        $scope.uiIsLoggingIn = false;
      });
    };
  })
;

