'use strict';

angular.module('DEVBYCM.cmLoadingModule', [])
  .directive('cmLoading', function (cmLoadingSvc,$rootScope) {
    return {
      restrict: 'E',
      templateUrl: 'app/components/ui/cmLoading/template.html',
      scope:{},
      link: function(scope) {
        scope.isShow=false;
        scope.content="";
        scope.status="";
        $rootScope.$on('cmLoading', function (e, v) {
          v && !angular.isUndefined(v['show']) && (scope.isShow = v['show']);
          v && !angular.isUndefined(v['content']) && (scope.content = v['content']);
          v && !angular.isUndefined(v['status']) && (scope.status = v['status']);
        });
      }
    };
  })
  .factory('cmLoadingSvc', function ($rootScope,$timeout) {
    var showingloadingList = [];
    var tempContent={};
    return {
      'show':function(){
        showingloadingList.push(tempContent);
        $rootScope.$broadcast('cmLoading', {'show': true});
      },
      'hide':function(delay){
        showingloadingList.pop();
        if(showingloadingList.length>0){
          $rootScope.$broadcast('cmLoading',showingloadingList[length-1]);
        }else{
          angular.isUndefined(delay) && (delay=1000);
          return $timeout(function(){
            $rootScope.$broadcast('cmLoading', {'show': false});
          },delay);
        }
      },
      'setContent':function(content,status){
        tempContent = {
          'content':content,
          'status':status
        };
        $rootScope.$broadcast('cmLoading',tempContent);
      }
    };
  })
;

'use strict';
angular.module('RMI.ProcessCtrls', [])
  .controller('ProcessListCtrl', function ($scope, $state, $filter, $timeout, FormSvc, ProcessSvc, cmNavBarService, cmLoadingSvc) {
    $scope.stateParams = $state.params;
    $scope.uiIsSideMenuListLoaded = false;
    (function funcLoadSideMenuList() {
      ProcessSvc.fetchProcessTypesByClass($scope.stateParams['Class']).then(function (processTypes) {
        $scope.sideMenuList = processTypes;
        cmLoadingSvc.setContent('加载完成');
      }, function (error) {
        console.log(error);
      }).finally(function () {
        $scope.uiIsSideMenuListLoaded = true;
        cmLoadingSvc.hide(750);
      });
    })();
    $scope.funcCreateForm = function () {
      cmLoadingSvc.setContent('收货报告创建中');
      cmLoadingSvc.show();
      FormSvc.requestCreate('ARRIVAL', 'ARRIVAL', 0, 'AR01').then(function (form) {
        $state.go('home.process.detailForm', {
          'ArrivalSerialNumber': form.ArrivalSerialNumber,
          'FormId': form.Id,
          'Mode': 'edit'
        });
      }, function (error) {
        cmLoadingSvc.setContent('创建过程发生错误，请检查网络连接');
        console.log(error);
      }).finally(function () {
        cmLoadingSvc.hide(750);
      });
    };
    $scope.funcGoDetail = function (ArrivalSerialNumber, FormId, Mode) {
      $state.go('home.process.detailForm', {
        'ArrivalSerialNumber': ArrivalSerialNumber,
        'FormId': FormId,
        'Mode': Mode
      });
    };
    $scope.funcGoDetailInfo = function (ArrivalSerialNumber) {
      $state.go('home.process.detailInfo', {'ArrivalSerialNumber': ArrivalSerialNumber});
    };
    $scope.funcGoFormEdit = function (Process, ProcessNode, ProcessNodeForm) {
      //表单不存在时候进行创建
      if (angular.equals(ProcessNodeForm, {})) {
        //从流程信息界面获取的表单编辑函数
        $scope.funcEditForm = function (ArrivalSerialNumber, FormId) {
          cmLoadingSvc.setContent('数据加载中');
          cmLoadingSvc.show();
          $state.go('home.process.detailForm', {'ArrivalSerialNumber': ArrivalSerialNumber, 'FormId': FormId});
          cmLoadingSvc.hide(100);
        };
        $scope.funcShortcutCreateFormAndRequestEditFormAndEditForm = function (ArrivalSerialNumber, ProcessTypeId, Rev, ProcessNodeTypeId) {
          cmLoadingSvc.setContent('正在尝试创建表单');
          cmLoadingSvc.show();
          FormSvc.requestCreate(ArrivalSerialNumber, ProcessTypeId, Rev, ProcessNodeTypeId).then(function (Form) {
            $state.reload();
            $scope.funcEditForm(ArrivalSerialNumber, Form.Id);
          }, function (error) {
            cmLoadingSvc.setContent('创建失败:' + error);
          }).finally(function () {
            cmLoadingSvc.hide(750);
          });
        };
        $scope.funcShortcutCreateFormAndRequestEditFormAndEditForm(Process.ArrivalSerialNumber, Process.TypeId, Process.Rev, ProcessNode.Id);
      } else {
        $scope.funcGoDetail(Process.ArrivalSerialNumber, ProcessNodeForm.Id, 'edit');
      }
    };
  })

