'use strict';
angular.module('RMI.HomeCtrls', [])
  .controller('HomeCtrl', function ($scope, $rootScope, $state) {
  })
  .controller('IndexCtrl', function ($scope, $http, $interval, AccountSvc, LoginSvc, FormSvc, ProcessSvc, $state, cmLoadingSvc, VERSION, cmLoginSvc) {
    $scope.version = 'VERSION: '+VERSION;
    $scope.currentUser = AccountSvc.currentUser();
    $scope.currentTime = new Date();
    $scope.quickGoProcessASN = '';
    var currentTimeUpdateTimer = $interval(function () {
      $scope.currentTime = new Date();
    }, 1000);
    $scope.funcAddArrivalForm = function () {
      cmLoadingSvc.setContent('收货报告创建中');
      cmLoadingSvc.show();
      FormSvc.requestCreate('ARRIVAL', 'ARRIVAL', 0, 'AR01').then(function (form) {
        $state.go('home.process.detailForm', {
          'ArrivalSerialNumber': form.ArrivalSerialNumber,
          'FormId': form.Id,
          'Mode': 'edit'
        });
      }, function (error) {
        cmLoadingSvc.setContent('创建过程发生错误，请检查网络连接');
        console.log(error);
      }).finally(function () {
        cmLoadingSvc.hide(750);
      });
    };
    $scope.funcQuickGoProcess = function (arrivalSerialNumber) {
      cmLoadingSvc.setContent('查询中...', 'inProgress');
      cmLoadingSvc.show();
      var funcCheckFormat = function (arrivalSerialNumber) {
        var pattern = /[0-9a-fA-F]{6}/;
        return pattern.test(arrivalSerialNumber);
      };
      if (funcCheckFormat(arrivalSerialNumber)) {
        ProcessSvc.fetchProcess(arrivalSerialNumber).then(function () {
          cmLoadingSvc.setContent('转跳中...');
          $state.go('home.process.detailInfo', {'ArrivalSerialNumber': arrivalSerialNumber});
        }, function () {
          cmLoadingSvc.setContent('该流水号不存在！');
        }).finally(function () {
          cmLoadingSvc.hide();
        });
      } else {
        cmLoadingSvc.setContent('流水号输入错误！', 'error');
        cmLoadingSvc.hide();
      }
    };
    $scope.$on('$destroy', function () {
      if (currentTimeUpdateTimer) {
        $interval.cancel(currentTimeUpdateTimer);
      }
    });
  })
;

