angular.module('RMI.ControlCtrls', [])
  .controller('ControlBaseCtrl', function ($scope, $rootScope, $state) {
    $scope.uiIsInDesignCacheEdit = false;
    $scope.state = $state;
    $rootScope.$on('$stateChangeStart',
      function (event, toState, toParams) {
        $scope.uiIsInDesignCacheEdit = !!toParams['isEditCache'];
      });
  })
  .controller('ControlDesignProcessCtrl', function ($scope, $state, $q, cmLoadingSvc, ControlDesignSvc) {
    $scope.processTypes = [];
    $scope.processTypes = [{
      'Id': 'X01',
      'Class': '辅料',
      'Name': '部件',
      'Description': '描述信息描述信息描述信息描述信息描述信息描述信息'
    }];
    (function () {
      cmLoadingSvc.setContent('正在加载所有流程列表', 'inProgress');
      cmLoadingSvc.show();
      ControlDesignSvc.fetchProcessTypes().then(function (ProcessTypes) {
        $scope.processTypes = ProcessTypes;
        cmLoadingSvc.setContent('加载成功', 'success');
      }, function () {
        cmLoadingSvc.setContent('加载失败');
      }).finally(function () {
        cmLoadingSvc.hide(750);
      });
    })();
    $scope.funcEditProcess = function (ProcessTypeId, Rev) {
      cmLoadingSvc.setContent('正在加载流程数据', 'inProgress');
      cmLoadingSvc.show();
      var processTypePromise = ControlDesignSvc.fetchProcessType(ProcessTypeId, Rev);
      var processNodeTypesPromise = ControlDesignSvc.fetchProcessNodeTypes(ProcessTypeId, Rev);
      var promise = $q.all([processTypePromise, processNodeTypesPromise]);
      promise.then(function (ProcessTypeAndProcessNodeTypes) {
        var ProcessType = ProcessTypeAndProcessNodeTypes[0];
        var ProcessNodeTypes = ProcessTypeAndProcessNodeTypes[1];
        angular.forEach(ProcessNodeTypes, function (ProcessNodeType) {
          ProcessNodeType.FormConfig = angular.fromJson(ProcessNodeType.FormConfig);
        });
        ControlDesignSvc.saveCacheConfig('process', ProcessNodeTypes, ProcessType);
        cmLoadingSvc.setContent('加载完成', 'success');
        $state.go('home.control.designEditProcess', {'isEditCache': 'cache', 'loadItemType': undefined});
      }, function (error) {
        cmLoadingSvc.setContent('数据加载错误', 'error');
        console.log(error);
      }).finally(function () {
        cmLoadingSvc.hide(750);
      });
    };
  })
  .controller('ControlDesignEditCtrl', function ($scope, $state, $filter, $q, ControlDesignSvc, FormSvc, cmLoadingSvc) {
    $scope.uiIsTable = false;
    $scope.uiIsMap = false;
    $scope.uiIsTableTemplateBaseShow = false;
    $scope.uiIsMapTemplateBaseShow = false;
    $scope.uiIsAddPanelShow = false;
    $scope.uiIsEditCache = $state.params.isEditCache ? true : false;
    var presetConfig = ControlDesignSvc.getPresetConfig();
    //var orderAttributeName = 'Order';

    //配置初始化
    $scope.configString = '';
    var pageType = '';
    if ($state.current.name === 'home.control.designEditMap') {
      $scope.uiIsTable = false;
      $scope.uiIsMap = true;
      pageType = 'map';
    } else if ($state.current.name === 'home.control.designEditTable') {
      $scope.uiIsTable = true;
      $scope.uiIsMap = false;
      pageType = 'table';
    } else if ($state.current.name === 'home.control.designEditForm') {
      pageType = 'form';
    } else if ($state.current.name === 'home.control.designEditProcess') {
      pageType = 'process';
    }
    $scope.generalConfig = presetConfig.general[pageType];
    $scope.itemConfig = presetConfig.item[pageType];
    $scope.itemConfigs = [];
    $scope.localConfigs = [];
    $scope.localConfigs = [{
      'description': new Date().getTime()
    }, {
      'description': new Date().getTime()
    }, {
      'description': new Date().getTime()
    }];

    //配置管理以及串接过程
    (function funcLoadConfigsFromCache(whetherToLoad) {
      if (!whetherToLoad)return 0;
      var cacheConfig = ControlDesignSvc.getCacheConfig(pageType);
      angular.extend($scope.generalConfig, cacheConfig.content.general);
      angular.extend($scope.itemConfigs, cacheConfig.content.items);
      angular.extend($scope.itemConfig, cacheConfig.content.item);
      var loadItemType = $state.params.loadItemType;
      if (loadItemType === 'form' || loadItemType === 'table' || loadItemType === 'map') {
        angular.extend($scope.itemConfig, ControlDesignSvc.getCacheItemConfigByChildConfig(loadItemType));
      }
    })(!!$state.params.isEditCache);
    $scope.funcSaveConfigsCacheAndBack = function () {
      cmLoadingSvc.setContent('正在保存编辑后的配置', 'inProgress');
      cmLoadingSvc.show();
      ControlDesignSvc.saveCacheConfig(pageType, $scope.itemConfigs, $scope.generalConfig);
      if (pageType === 'map' || pageType === 'table') {
        $state.go('home.control.designEditForm', {'isEditCache': 'cache', 'loadItemType': pageType});
        cmLoadingSvc.hide(750);
      } else if (pageType === 'form') {
        $state.go('home.control.designEditProcess', {'isEditCache': 'cache', 'loadItemType': pageType});
        cmLoadingSvc.hide(750);
      } else if (pageType === 'process') {
        var updateProcessTypePromise = ControlDesignSvc.updateProcessType($scope.generalConfig.Id, $scope.generalConfig);
        var updateProcessNodeTypesPromise = ControlDesignSvc.updateProcessNodeTypes($scope.generalConfig.Id, $scope.itemConfigs);
        var promise = $q.all([updateProcessTypePromise, updateProcessNodeTypesPromise]);
        promise.then(function () {
          cmLoadingSvc.setContent('保存成功', 'success');
          $state.go('home.control.designProcess', {'isEditCache': 'cache'});
        }, function (error) {
          console.log(error);
          cmLoadingSvc.setContent('保存失败', 'error');
        }).finally(function () {
          cmLoadingSvc.hide(750);
        });
      }
    };

    var currentEditItemConfigIndex = null;
    $scope.funcLoadItemConfigToAddPanel = function (itemConfig, index) {
      currentEditItemConfigIndex = index;
      $scope.itemConfig = angular.copy(itemConfig);
      console.log('Current Editing:' + index);
    };
    var funcGoDesignEdit = function (goPageType) {
      ControlDesignSvc.saveCacheConfig(pageType, $scope.itemConfigs, $scope.generalConfig, null);
      ({
        'form': function () {
          $state.go('home.control.designEditForm', {'isEditCache': 'cache'});
        },
        'table': function () {
          $state.go('home.control.designEditTable', {'isEditCache': 'cache'});
        },
        'map': function () {
          $state.go('home.control.designEditMap', {'isEditCache': 'cache'});
        }
      }[goPageType])();
    };
    $scope.funcGoEditItemConfig = function (goPageType) {
      var itemConfig = $scope.itemConfig;
      var order = currentEditItemConfigIndex;
      if (itemConfig) {
        if (pageType === 'process') {
          ControlDesignSvc.saveItemCacheConfig('process', itemConfig);
          ControlDesignSvc.saveCacheConfigByFatherConfig(itemConfig, 'form', order);
        } else if (pageType === 'form') {
          ControlDesignSvc.saveItemCacheConfig('form', itemConfig);
          var itemConfigType = itemConfig.Type;
          if (itemConfigType === 'table') {
            ControlDesignSvc.saveCacheConfigByFatherConfig(itemConfig, 'table', order);
          } else if (itemConfigType === 'map') {
            ControlDesignSvc.saveCacheConfigByFatherConfig(itemConfig, 'map', order);
          }
        }
      }
      funcGoDesignEdit(goPageType);
    };
    $scope.funcGoCreateItemConfig = function (goPageType) {
      funcGoDesignEdit(goPageType);
    };

    //添加面板的工具函数
    $scope.funcPackFormConfigString = function (configString) {
      var formConfig = angular.fromJson(configString);
      var formConfigPack = {
        'FormConfig': formConfig
      };
      return angular.toJson(formConfigPack);
    };
    //configs列表表格的操作函数
    $scope.funcLoadConfig = function (configString) {
      var config = angular.fromJson(configString);
      angular.forEach(config, function (v, k) {
        $scope.itemConfig[k] = v;
      });
    };
    $scope.funcAddUpConfigs = function () {
      $scope.itemConfig['Order'] = $scope.itemConfigs.length + 1;
      $scope.itemConfigs.push(angular.copy($scope.itemConfig));
      $scope.itemConfig = presetConfig.item[pageType];
    };
    $scope.funcReorderConfigs = function () {
      $scope.itemConfigs = $filter('orderBy')($scope.itemConfigs, 'Order');
      angular.forEach($scope.itemConfigs, function (v, k) {
        v['Order'] = k + 1;
      });
    };
    $scope.funcDeleteFromConfigs = function (index) {
      $scope.itemConfigs.splice(index, 1);
    };

    //流程编辑工具中的操作函数
    $scope.funcAddProcessTypeIdToConfigs = function () {
      angular.forEach($scope.itemConfigs, function (v) {
        v.ProcessTypeId = $scope.generalConfig.Id;
        v.Rev = $scope.generalConfig.Rev;
      });
    };
    $scope.funcAddDatabaseTableNameToFormConfigs = function () {
      var databaseTableNameStr = 'RMI_FORM_DATA_PROCESSTYPEID_REVREPLACE_REV_PROCESSNODETYPEID_COMPONENTORDER_TYPE';
      angular.forEach($scope.itemConfigs, function (v) {
        if (typeof (v.FormConfig) === 'object') {
          var databaseTableNameStr2 = databaseTableNameStr
            .replace(/PROCESSTYPEID/g, v.ProcessTypeId).replace(/PROCESSNODETYPEID/g, v.Id).replace(/REPLACE_REV/g, v.Rev);
          angular.forEach(v.FormConfig['Components'], function (va) {
            va.DatabaseTableName = databaseTableNameStr2.replace(/ORDER/g, va.Order).replace(/TYPE/g, va.Type.toUpperCase());
          });
        }
      });
    };
    $scope.funcCreateProcessTypeAndProcessNodeTypes = function () {
      cmLoadingSvc.setContent('正在提交流程数据');
      cmLoadingSvc.show();
      $scope.funcAddProcessTypeIdToConfigs();
      $scope.funcAddDatabaseTableNameToFormConfigs();
      var processType = $scope.generalConfig;
      var processNodeTypes = $scope.itemConfigs;
      angular.forEach(processNodeTypes, function (v) {
        //转成字符串以方便服务器存储到数据库
        if (typeof(v.FormConfig) !== 'string') {
          //v.FormConfig = angular.toJson(v.FormConfig);
        }
        if (typeof(v.Order) === 'number') {
          v.Order = v.Order.toString();
        }
      });
      var createProcessTypePromise = ControlDesignSvc.createProcessType(processType);
      console.log(processNodeTypes);
      var createProcessNodeTypesPromise = ControlDesignSvc.createProcessNodeTypes(processNodeTypes);
      var promise = $q.all([createProcessTypePromise, createProcessNodeTypesPromise]);
      promise.then(function () {
        cmLoadingSvc.setContent('提交成功！');
      }, function (error) {
        cmLoadingSvc.setContent('提交失败');
        console.log('funcCreateProcessTypeAndProcessNodeTypes Error:' + error);
      }).finally(function () {
        cmLoadingSvc.hide(750);
      })
    };
  })
