(function () {
  'use strict';
  var unique = 1;

  angular
    .module('RMI')
    .config(formlyCollectionConfig)
    .directive('cmFormlyTable', function () {
      return {
        restrict: 'E',
        template: '<div class="cmFormCollection cmTable {{hideRepeat}} cm-animation-fade" style="{{uiIsEmptyTableInitial?\'min-height:163px;max-height:163px;\':\'\'}}">\n  <div class="loading-mask" ng-if="uiIsEmptyTableInitial">\n    <div class="text">Loading...</div>\n  </div>\n  <div>\n    <header class="table-head">\n      <div style="min-width: {{rowModifyClassWidth+\'px\'}};max-width: {{rowModifyClassWidth+\'px\'}};height: 100%;"></div>\n      <div class="head-cell" ng-repeat="cell in tableHeadCells track by $index" style="width: {{cell.width+\'px\'}}">\n        {{cell.name}}\n      </div>\n    </header>\n    <section class="table-body">\n      <div class="table-row" ng-repeat="element in model" ng-init="fields = funcCopyFields(to.fields)">\n        <div class="row-modify">\n          <a ng-click="funcRemoveWithConfirm($index)">删除</a>\n        </div>\n        <formly-form class="row-content" fields="fields" model="element" form="form"></formly-form>\n      </div>\n      <div class="table-row-no-content" ng-show="model.length===0">暂无内容</div>\n    </section>\n    <footer class="table-foot">\n      <div class="addNewRowButton">\n        <button type="button" class="btn btn-block btn-primary" ng-click="funcPushWithBringDown()">添加新的项目</button>\n      </div>\n    </footer>\n  </div>\n</div>',
        replace: true,
        scope: {
          model: '=',
          to: '=',
          templateOptions: '='
        },
        link: function (scope, el) {
          var funcSetTableHeader = function () {
            setTimeout(function () {
              scope.rowModifyClassWidth = $($(el).find(".row-modify")[0]).outerWidth(true);
              var firstFormly = $(el).find(".formly")[0];
              var formlyFields = $(firstFormly).find(".formly-field");
              scope.tableHeadCells = formlyFields.map(function () {
                return {'width': $(this).outerWidth(true)};
              }).get();
              angular.forEach(scope.tableHeadCells, function (cell, index) {
                var key = scope.to.fields[index].key;
                angular.forEach(scope.to.relation, function (relation) {
                  if (relation.key === key) {
                    cell.name = relation.name;
                  }
                })
              });
              scope.$digest();
            }, 0)
          };
          //绑定页面大小变化的时候，重新计算表头宽度
          $(window).bind("resize", function () {
            el.ready(function () {
              funcSetTableHeader();
            });
          });
          setInterval(function () {
            el.ready(function () {
              funcSetTableHeader();
            });
          }, 750);
          //表格行数发生变化的时候，重新绑定回车事件
          scope.$watch(function () {
            if (!angular.isUndefined(scope.model)) {
              return scope.model.length;
            }
            return 0;
          }, function () {
            scope.uiIsLoading = true;
            //绑定回车换格功能
            setTimeout(function () {
              var rows = $(el).find(".formly");
              rows.map(function (rowIndex) {
                $(this).find('.formly-field').map(function () {
                  var nextFormLyField = $(this).next();
                  $(this).find('input').unbind('keypress');
                  $(this).find('input').bind('keypress', function (e) {
                    var funcGoToNextRowBegin = function (rowIndex, rowsLength) {
                      if (rowIndex <= rowsLength - 1) {
                        $(rows[rowIndex + 1]).find('.formly-field').find('input').first().focus();
                      } else {
                        scope.funcPushWithBringDown();
                        scope.$apply();
                        newRows = $(el).find(".formly");
                        $(newRows[rowIndex + 1]).find('.formly-field').find('input').first().focus();
                      }
                    };
                    var newRows = null;
                    if (e.ctrlKey === true && e.keyCode === 10) {
                      e.preventDefault();
                      funcGoToNextRowBegin(rowIndex, rows.length - 1);
                    } else if (e.keyCode === 13) {
                      e.preventDefault();
                      var nextInput = nextFormLyField.find('input');
                      if (nextInput.length) {
                        nextInput.focus();
                      } else {
                        funcGoToNextRowBegin(rowIndex, rows.length - 1);
                      }
                    }
                  });
                });
              });
              setTimeout(function () {
                scope.uiIsLoading = false;
                scope.$digest();
              }, 0);
            }, 0);
          });
          //开始初始化表格，初始化遮罩
          scope.uiIsEmptyTableInitial = !(scope.model);
          scope.uiIsLoading = true;
          //初始化表头，并结束加载
          if (1 || scope.uiIsEmptyTableInitial) {
            //通过添加一行来确保能够获取到表头的宽度
            scope.funcPush();
            //初始化表头宽度后再删除多余的一行
            setTimeout(function () {
              funcSetTableHeader();
              scope.funcPop();
              scope.uiIsEmptyTableInitial = false;
              setInterval(function () {
                scope.uiIsInitial = false;
              }, 0);
            }, 0);
          } else {
            funcSetTableHeader();
            scope.uiIsInitial = false;
          }
        },
        controller: function ($scope) {
          $scope.formOptions = {formState: $scope.formState};
          $scope.funcRemoveWithConfirm = function (index) {
            var result = confirm("您确定要删除这一项吗？");
            if (result) {
              $scope.funcRemove(index);
            }
          };
          $scope.funcRemove = function (index) {
            $scope.model.splice(index, 1);
          };
          $scope.funcPop = function () {
            $scope.model.pop();
          };
          $scope.funcPush = function () {
            $scope.model = $scope.model || [];
            $scope.model.push({});
          };
          $scope.funcPushWithBringDown = function () {
            $scope.model = $scope.model || [];
            var rows = $scope.model;
            var lastRow = rows[rows.length - 1];
            var newRow = {};
            if (lastRow) {
              angular.forEach($scope.to.bringDown, function (bringDownOption) {
                if (lastRow[bringDownOption['key']]) {
                  newRow[bringDownOption['key']] = angular.copy(lastRow[bringDownOption['key']]);
                }
              });
            }
            rows.push(newRow);
          };
          $scope.funcCopyFields = copyFields;
          function copyFields(fields) {
            fields = angular.copy(fields);
            addRandomIds(fields);
            return fields;
          }

          function addRandomIds(fields) {
            unique++;
            angular.forEach(fields, function (field, index) {
              if (field.fieldGroup) {
                addRandomIds(field.fieldGroup);
                return; // fieldGroups don't need an ID
              }

              if (field.templateOptions && field.templateOptions.fields) {
                addRandomIds(field.templateOptions.fields);
              }

              field.id = field.id || (field.key + '_' + index + '_' + unique + getRandomInt(0, 9999));
            });
          }

          function getRandomInt(min, max) {
            return Math.floor(Math.random() * (max - min)) + min;
          }
        }
      }
    });

  /** @ngInject */
  function formlyCollectionConfig(formlyConfigProvider) {
    formlyConfigProvider.setType({
      name: 'cm-input',
      template: '<div class="cmFormCollection cmInput">\n  <label>\n    <div class="input-label">\n      {{options.templateOptions.label}}\n    </div>\n    <input type="{{options.templateOptions.type}}" ng-model="model[options.key]">\n  </label>\n</div>',
      link: function (scope) {
        /*        if(scope.options.templateOptions.type==='date'){
         console.log(0);
         scope.model= new Date(scope.model);
         }*/
      }
    });
    formlyConfigProvider.setType(
      {
        name: 'cm-select',
        template: '<div class="cmFormCollection cmSelect" style="width: {{options.templateOptions.width}}">\n  <label>\n    {{options.templateOptions.label}}\n  </label>\n  <div>\n    <div>\n      <ol class="nya-bs-select" ng-model="model[options.key || index]"\n          style="width: {{options.templateOptions.selectWidth}}"\n          title="请选择"\n          live-search="options.templateOptions.liveSearch===\'true\'"\n          disabled="options.templateOptions.options.length === 0">\n        <li ng-show="options.templateOptions.group"\n            nya-bs-option="option in  options.templateOptions.options group by option.group"\n            data-value="option.value"\n        >\n          <span class="dropdown-header">{{$group}}</span>\n          <a><span>{{option.name}}</span><span class="">1</span></a></li>\n        <li ng-show="!options.templateOptions.group"\n            nya-bs-option="option in  options.templateOptions.options"\n            data-value="option.value"\n        ><span\n          class="dropdown-header">{{$group}}</span>\n          <a><span>{{option.name}}</span><span class="">1</span></a></li>\n      </ol>\n    </div>\n  </div>\n</div>',
      }
    );
    formlyConfigProvider.setType({
      name: 'cm-table',
      template: '<cm-formly-table model="model[options.key]" to="to" templateOptions="templateOptions"></cm-formly-table>',
    });
  }

})();

