angular.module('DEVBYCM.cmCheckbox', [])
  .directive('cmCheckbox', function (UtilSvc) {
    return {
      restrict: 'E',
      template: '<div class="cmFormCollection cmCheckbox">\n  <label ng-repeat="option in options">\n    <input type="checkbox" ng-model="value[$index]" ng-click="funcUpdateModel(value[$index],index,option)" ng-value="option">{{option}}\n  </label>\n</div>',
      scope: {
        model: '=',
        options: '='
      },
      replace: true,
      link: function (scope) {
        if(!angular.isArray(scope.model)){
          scope.model = [];
        }
        scope.name = UtilSvc.random(16,8,true,false);
        var funcFindOptionIndex = function(array,item){
          for(var i =0;i<array.length;i++){
            if(array[i]===item){
              return i;
            }
          }
          console.log('Cant find item in the array.')
        };
        scope.funcUpdateModel = function(value,index,option){
          if(value){
            scope.model.push(option);
          }else{
            if(angular.isNumber(funcFindOptionIndex(scope.model,option))){
              scope.model.splice(funcFindOptionIndex(scope.model,option),1);
            }
          }
        }
      }
    }
  })
;

