'use strict';

angular.module('RMI.UniversalServices', [])
  .factory('UtilSvc',function(){
    return {
      'random':function(radix,digit,isUpperCase,isFillZero){
        if(radix<1||radix>36||digit<0||digit>16){
          console.log('UtilSvc.random Error: radix='+radix+' digit='+digit+' isUppercase='+isUpperCase+' isFillZero='+isFillZero);
          return -1;
        }
        var random = Math.round(Math.random()*Math.pow(radix,digit)).toString(radix).toLowerCase();
        if(isUpperCase){
          random=random.toUpperCase()
        }
        if(isFillZero&&random.length!==(digit-1)){
          random = random + (new Array(digit-random.length).join('0'));
        }
        return random;
      }
    };
  })
  .factory('KVStoreSvc', function ($rootScope,$q,$http,SERVER_URL) {
    var SERVER_KVSTORE_URL = SERVER_URL+'kvstore/';
    return {
      'create':function(k,v){
        var deferred = $q.defer();
        $http.post(SERVER_KVSTORE_URL+k+'/',angular.toJson(v))
          .success(function(v){
          deferred.resolve(angular.fromJson(v));
        }).error(function(error){
          deferred.reject(error);
        });
        return deferred.promise;
      },
      'read':function(k){
        var deferred = $q.defer();
        $http.get(SERVER_KVSTORE_URL+k+'/')
          .success(function(v){
          deferred.resolve(angular.fromJson(v));
        }).error(function(error){
          deferred.reject(error);
        });
        return deferred.promise;
      },
      'update':function(k,v){
        var deferred = $q.defer();
        $http.put(SERVER_KVSTORE_URL+k+'/',angular.toJson(v))
          .success(function(v){
          deferred.resolve(angular.fromJson(v));
        }).error(function(error){
          deferred.reject(error);
        });
        return deferred.promise;
      },
      'delete':function(k){
        var deferred = $q.defer();
        $http.delete(SERVER_KVSTORE_URL+k+'/')
          .success(function(v){
          deferred.resolve(angular.fromJson(v));
        }).error(function(error){
          deferred.reject(error);
        });
        return deferred.promise;
      }
    }
  })
  .factory('SQLQuerySvc',function($q,$http,SERVER_URL){
    return {
      'query':function(sql){
        var deferred = $q.defer();
        $http.get(SERVER_URL+'sqlquery',{'params':{'sql':sql}})
          .success(function(ResultSet){
            deferred.resolve(ResultSet);
          }).error(function(error){
            deferred.reject(error)}
        );
        return deferred.promise;
      }
    };
  })
;

