        KVStoreSvc.read(KVStoreSourceKey).then(function (Sources) {
          angular.forEach(Sources, function (v, k) {
            if (v.Name === sourceName) {
              Sources.splice(k, 1);
            }
          });
          KVStoreSvc.update(KVStoreSourceKey, Sources).then(function (newSources) {
            deferred.resolve(newSources);
          }, function (error) {
            deferred.reject(error);
          });
        }, function (error) {
          deferred.reject(error);
        });
        return deferred.promise;
