'use strict';

angular.module('RMI.HomeServices', [])
  .factory('LoginSvc', function ($q, $http, SERVER_URL,cmLoginSvc, AccountSvc,$interval,$timeout) {
    var isLoggedIn = 'false';
    //TODO:多用户的问题
    isLoggedIn = document.cookie.split("=")[1] == 'true';
    console.log(document.cookie);
    var funcCheckAndShowLoginPanel = function(){
      if(isLoggedIn === false){
        cmLoginSvc.show();
      }
    };
    $timeout(function(){
      funcCheckAndShowLoginPanel();
    });
    $interval(function(){
      funcCheckAndShowLoginPanel();
    },5*60*1000);
    return {
      'isLoggedIn': function () {
        return isLoggedIn;
      },
      'login': function (userId, password) {
        var deferred = $q.defer();
        $http.post(SERVER_URL + 'users/' + userId + '/login/', {'Id': userId, 'Password': password})
          .success(function (User) {
            isLoggedIn = true;
            //保存当前用户
            AccountSvc.setCurrentUser(User);
            localStorage.setItem("LoginSvc.isLoggedIn",true);
            document.cookie = "isLoggedin=true";
            //
            deferred.resolve(User);
          })
          .error(function (error) {
            deferred.reject(error);
          });
        return deferred.promise;
      },
      'logout': function () {
        var userId = AccountSvc.currentUser().ID;
        var deferred = $q.defer();
        $http.post(SERVER_URL + 'users/' + userId + '/logout/')
          .success(function () {
            isLoggedIn = false;
            localStorage.setItem("LoginSvc.isLoggedIn",false);
            deferred.resolve();
            $timeout(function(){
              funcCheckAndShowLoginPanel();
            },1000);
          })
          .error(function () {
            deferred.reject();
          });
        return deferred.promise;
      },
      'getSavedUsers': function () {
        var savedUsers = angular.fromJson(localStorage.getItem('Account.SavedUsers'));
        if (!angular.isArray(savedUsers)) {
          savedUsers = [];
        }
        return savedUsers;
      },
      'setSavedUsers': function (outerSavedUsers) {
        if (angular.isArray(outerSavedUsers)) {
          localStorage.setItem('Account.SavedUsers', angular.toJson(outerSavedUsers));
        }
      }
    };
  })
